import React, { useState } from "react";
import { useStyles } from "./styles";
import { Page1 } from "../Pages/Page1";
import { Page2 } from "../Pages/Page2";
import { Page3 } from "../Pages/Page3";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import Button from "@material-ui/core/Button";

export const Viewer = () => {
  const [page, setPage] = useState<number>(0);

  const handleIncrease = () => setPage(page === 2 ? 0 : page + 1);
  const handleDecrease = () => setPage(page === 0 ? 2 : page - 1);

  const pages = [<Page1 />, <Page2 />, <Page3 />];

  const classes = useStyles();
  return (
    <div className={classes.root}>
      <div style={{ alignSelf: "center", width: 0, height: 0 }}>
        <Button className={classes.menuButtonLeft} onClick={handleDecrease}>
          <ChevronLeftIcon
            style={{
              transform: "rotate(-45deg) translateX(15px)",
              color: "white",
            }}
            fontSize={"large"}
          />
        </Button>
      </div>

      <div style={{ overflow: "auto", width: "100%" }}>{pages[page]}</div>
      <div style={{ alignSelf: "center", width: 0, height: 0 }}>
        <Button className={classes.menuButtonRight} onClick={handleIncrease}>
          <ChevronRightIcon
            style={{
              transform: "rotate(-45deg) translateX(-15px)",
              color: "white",
            }}
            fontSize={"large"}
          />
        </Button>
      </div>
    </div>
  );
};
